body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(228, 238, 188);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cont{
  background-color: #395a72;
  height: 400px;
}

.temp{
  border-radius: 50%;
  /* background-color: #578eb6; */
  margin-top: 20px;
  width: 200px;
  height: 200px;
  position: relative;
  color: white;
  border: 05px solid white;
}

.butt
{
  border: 4px solid white;
  border-radius: 50%;
  background-color: #578eb6;
  margin-top: 20px;
  width: 100px;
  height: 100px;
  position: relative;
  cursor: grab;
}

.cold{
  background-color: #088eb1;
}

.warm{
  background-color: #dd5d22;
}

@media only screen and (max-width: 768px) {
 .cont{
  height: 600px;
 }
}